import React, { Fragment, Component } from 'react';
import PropTypes from 'prop-types';
import {
  DropdownToggle,
  DropdownItem,
  DropdownMenu,
  Dropdown,
  Nav
} from 'reactstrap';
import {
  AppHeaderDropdown,
  AppSidebarToggler,
  AppNavbarBrand
} from '@coreui/react';

import sygnet from '../../assets/img/brand/sygnet.svg';
import logo from '../../assets/img/brand/logo.svg';

class DefaultHeader extends Component {
  state = {
    dropdownOpen: false
  }

  onToggleDropdown = () => {
    this.setState(prevState => ({ dropdownOpen: !prevState.dropdownOpen }));
  };

  render() {
    const { dropdownOpen } = this.state;

    return (
      <Fragment>
        <AppSidebarToggler className="d-lg-none" display="md" mobile />
        <AppNavbarBrand
          full={{
            src: logo, width: 89, height: 25, alt: 'CoreUI Logo'
          }}
          minimized={{
            src: sygnet, width: 30, height: 30, alt: 'CoreUI Logo'
          }}
        />
        <AppSidebarToggler className="d-md-down-none" display="lg" />
        <div className="header-title">
          Реэстр суб'єктів, що надають послуги
        </div>
        <Nav className="ml-auto" navbar>
          <Dropdown nav isOpen={dropdownOpen} toggle={this.onToggleDropdown}>
            <DropdownToggle nav style={{ color: 'white', marginRight: 20 }}>
              <strong style={{ marginRight: 10 }}>
                Адміністратор
              </strong>
              <i className="fa fa-user" style={{ fontSize: 18 }} />
            </DropdownToggle>
            <DropdownMenu right>
              <DropdownItem>
                <i className="icon-notebook" />
                Перейти в профіль
              </DropdownItem>
              <DropdownItem>
                <i className="icon-logout" />
                Вийти
              </DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </Nav>
      </Fragment>
    );
  }
}

DefaultHeader.propTypes = {
};

DefaultHeader.defaultProps = {
};

export default DefaultHeader;



