import React from 'react';
import Loadable from 'react-loadable';

import { DefaultLayout } from './components';

import {
  Dictionaries,
  Dictionary1,
  Dictionary2,
  Users
} from './pages';

import { PATH } from './constants';

const routes = [
  {
    path: PATH.ROOT,
    name: 'Home',
    component: DefaultLayout,
    exact: true
  },
  {
    path: PATH.USERS,
    name: 'Users',
    component: Users
  },
  {
    path: PATH.DICTIONARIES,
    name: 'Dictionaries',
    component: Dictionaries
  },
  {
    path: `${PATH.DICTIONARIES}_1`,
    name: 'Dictionary 1',
    component: Dictionary1
  },
  {
    path: `${PATH.DICTIONARIES}_2`,
    name: 'Dictionary 2',
    component: Dictionary2
  }
];

export default routes;



