import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';

import {
  Button,
  Select,
  Input,
  Card
} from '../UI';

class Filters extends Component {
  state = {
    value1: '',
    value2: ''
  };

  onSelect1 = value1 => {
    this.setState({ value1 });
  };

  onSelect2 = value2 => {
    this.setState({ value2 });
  };

  render() {
    const { value1, value2 } = this.state;

    return (
      <Card
        isOpenByDefault={false}
        collapsable
        style={{ marginBottom: 10 }}
        header={(
          <strong>
            Пошук користувачів
          </strong>
        )}
      >
        <Col lg="9" md="8" sm="12" xs="12" style={{ paddingLeft: 0, paddingRight: 0 }}>
          <Row>
            <Col lg="8" md="8" sm="12" xs="12">
              <Select
                label="Організація"
                value={value1}
                onSelect={this.onSelect1}
              />
            </Col>
            <Col lg="4" md="4" sm="12" xs="12">
              <Select
                label="Роль"
                value={value2}
                onSelect={this.onSelect2}
              />
            </Col>
          </Row>
          <Row>
            <Col lg="4" md="4" sm="12" xs="12">
              <Input
                label="Прізвище"
                onChangeText={e => console.log(e.target.value)}
              />
            </Col>
            <Col lg="4" md="4" sm="12" xs="12">
              <Input
                label="Ім'я"
                onChangeText={e => console.log(e.target.value)}
              />
            </Col>
            <Col lg="4" md="4" sm="12" xs="12">
              <Input
                label="По батькові"
                onChangeText={e => console.log(e.target.value)}
              />
            </Col>
          </Row>
          <Row>
            <Col className="mt-3">
              <Button label="Пошук" icon="fa fa-search" />
            </Col>
          </Row>
        </Col>
        <Col xs="3" style={{ paddingLeft: 0 }} />
      </Card>
    );
  }
}

export default Filters;



