import React, { Component } from 'react';
import propTypes from 'prop-types';
import {
  CardHeader,
  CardFooter,
  Collapse,
  CardBody,
  Card
} from 'reactstrap';

import Button from './Button';

import { COLORS } from '../../constants';

class CustomCard extends Component {
  state = {
    isOpen: this.props.isOpenByDefault
  }

  onToggle = () => {
    this.setState(prev => ({ isOpen: !prev.isOpen }));
  };

  renderCollapseIcon = () => (
    <a className="card-header-action btn btn-minimize" style={{ paddingRight: 10 }} onClick={this.onToggle}>
      <i className={this.state.isOpen ? 'icon-arrow-down' : 'icon-arrow-up'} />
    </a>
  );

  renderHeader = () => {
    const { collapsable, header, headerStyle } = this.props;

    return (
      <CardHeader style={headerStyle}>
        { collapsable ? this.renderCollapseIcon() : null }
        { header }
      </CardHeader>
    );
  };

  renderFooter = () => {
    const { footerStyle, footer } = this.props;

    return (
      <CardFooter style={footerStyle}>
        {footer}
      </CardFooter>
    );
  };

  render() {
    const { isOpen } = this.state;
    const {
      bodyStyle,
      children,
      header,
      footer,
      style
    } = this.props;

    return (
      <Card style={style}>
        { header ? this.renderHeader() : null }
        <Collapse isOpen={isOpen}>
          <CardBody style={bodyStyle}>
            {children}
          </CardBody>
        </Collapse>
        { footer ? this.renderFooter() : null }
      </Card>
    );
  }
}

CustomCard.propTypes = {
  isOpenByDefault: propTypes.bool,
  collapsable: propTypes.bool,
  headerStyle: propTypes.object,
  footerStyle: propTypes.object,
  bodyStyle: propTypes.object,
  children: propTypes.node,
  header: propTypes.node,
  footer: propTypes.node,
  style: propTypes.object,
};

CustomCard.defaultProps = {
  isOpenByDefault: true,
  collapsable: false,
  headerStyle: {},
  footerStyle: {},
  bodyStyle: {},
  children: null,
  header: null,
  footer: null,
  style: {},
};

export default CustomCard;



