import React, { Component } from 'react';
import { Container } from 'reactstrap';
import uuid from 'uuid';
import { Redirect, Switch, Route } from 'react-router-dom';

import {
  AppSidebarMinimizer,
  AppSidebarFooter,
  AppSidebarHeader,
  AppSidebarForm,
  AppSidebarNav,
  AppSidebar,
  AppFooter,
  AppHeader,
  AppAside
} from '@coreui/react';

import DefaultFooter from './DefaultFooter';
import DefaultHeader from './DefaultHeader';

import routes from '../../routes';

import { NAVIGATION, PATH } from '../../constants';

class DefaultLayout extends Component {
  renderRoutes = () => (
    <Switch>
      {routes.map(route => {
        return route.component ? (
          <Route
            key={uuid.v4()}
            path={route.path}
            name={route.name}
            exact={route.exact}
            render={props => <route.component {...props} />}
          />
        ) : null;
      })}
      <Redirect from={PATH.ROOT} to={PATH.USERS} />
    </Switch>
  );

  render() {
    return (
      <div className="app">
        <AppHeader fixed>
          <DefaultHeader />
        </AppHeader>
        <div className="app-body">
          <AppSidebar fixed display="lg">
            <AppSidebarHeader />
            <AppSidebarForm />
            <AppSidebarNav navConfig={{ items: NAVIGATION }} {...this.props} />
            <AppSidebarFooter />
            {/*  <AppSidebarMinimizer /> */}
          </AppSidebar>
          <main className="main">
            <Container fluid>
              {this.renderRoutes()}
            </Container>
          </main>
        </div>
        {/*
          <AppFooter>
            <DefaultFooter />
          </AppFooter>
        */}
      </div>
    );
  }
}

export default DefaultLayout;



