import React, { Component, Fragment } from 'react';

import { Card, Accordion } from '../UI';

import { COLORS } from '../../constants';

/* eslint-disable */
const dummy = [
  {
    label: 'Користувач',
    component: <p>Користувач</p>
  },
  {
    label: 'Реєстратор',
    component: <p>Реєстратор</p>
  },
  {
    label: 'Адміністратор',
    component: <p>Адміністратор</p>
  },
  {
    label: 'Адміністратор 2',
    component: <p>Адміністратор 2</p>
  }
];

class Roles extends Component {

  renderCardHeader = () => (
    <Fragment>
      <strong>
        Ролі
      </strong>
      <div className="card-header-actions">
        <a className="card-header-action btn btn-minimize" style={{ color: COLORS.BLUE }}>
          <i className="fa fa-plus" style={{ marginRight: 5 }} />
          <strong>
            Додати
          </strong>
        </a>
      </div>
    </Fragment>
  );

  render() {
    return (
      <Card header={this.renderCardHeader()} collapsable>
        <Accordion items={dummy} />
      </Card>
    );
  }
}

export default Roles;



