import React, { Component, Fragment } from 'react';
import { Col, Row, Form } from 'reactstrap';
import { AppSwitch } from '@coreui/react';

import {
  Select,
  Input,
  Card
} from '../UI';

import { COLORS } from '../../constants';

class UsersItem extends Component {
  state = {
    value1: '',
    value2: ''
  };

  onSelect1 = value1 => {
    this.setState({ value1 });
  };

  onSelect2 = value2 => {
    this.setState({ value2 });
  };

  renderCardFooter = () => (
    <Fragment>
      <AppSwitch
        className="mx-1 footer-switch"
        style={{ float: 'left', paddingTop: 7 }}
        color="primary"
        checked
        variant="pill"
      />
      <div style={{ float: 'right' }}>
        <a className="card-footer-action btn btn-minimize" style={{ color: COLORS.BLUE }}>
          <i className="fa fa-edit" style={{ marginRight: 5 }} />
          <strong>
            Редагувати
          </strong>
        </a>
      </div>
    </Fragment>
  );

  render() {
    return (
      <Card footer={this.renderCardFooter()} style={{ backgroundColor: 'rgba(91, 119, 158, 0.03)' }}>
        <Row>
          <Form style={{ paddingLeft: 5, paddingRight: 5 }}>
            <Input
              required
              disabled
              labelPosition="left"
              label="Прізвище"
              onChangeText={e => console.log(e.target.value)}
            />
            <Input
              required
              disabled
              labelPosition="left"
              label="Ім’я"
              onChangeText={e => console.log(e.target.value)}
            />
            <Input
              disabled
              labelPosition="left"
              label="По батькові"
              onChangeText={e => console.log(e.target.value)}
            />
            <Select
              required
              disabled
              labelPosition="left"
              label="Роль"
              value={this.state.value1}
              onSelect={this.onSelect1}
            />
            <Select
              disabled
              required
              labelPosition="left"
              label="Організація"
              value={this.state.value2}
              onSelect={this.onSelect2}
            />
          </Form>
        </Row>
      </Card>
    );
  }
}

export default UsersItem;



