import React, { Component } from 'react';
import propTypes from 'prop-types';
import {
  CardHeader,
  Collapse,
  CardBody,
  Card
} from 'reactstrap';

import { COLORS } from '../../constants';

class CustomAccordion extends Component {
  state = {
    accordion: Array(this.props.items.length).fill(false)
  }

  toggleAccordion = tab => {
    this.setState(prevState => ({
      accordion: prevState.accordion.map((x, i) => tab === i ? !x : false)
    }));
  };

  render() {
    const { accordion } = this.state;
    const {
      styleItem,
      style,
      items
    } = this.props;

    return (
      <div id="accordion" style={style}>
        {
          items.map((item, index) => (
            <Card key={index} style={[{ marginBottom: accordion.length === index + 1 ? 0 : 10 }, styleItem]}>
              <CardHeader id={`heading_${index}`} style={{ backgroundColor: COLORS.BLUE, color: COLORS.WHITE }}>
                <a
                  aria-expanded={accordion[index]}
                  aria-controls={`collapse_${index}`}
                  className="card-header-action btn btn-minimize"
                  style={{ paddingRight: 10, color: COLORS.WHITE }}
                  onClick={() => this.toggleAccordion(index)}
                >
                  <i className={accordion[index] ? 'icon-arrow-down' : 'icon-arrow-up'} />
                </a>
                <strong>
                  {item.label}
                </strong>
              </CardHeader>
              <Collapse
                isOpen={accordion[index]}
                aria-labelledby={`heading_${index}`}
                data-parent="#accordion"
                id={`collapse_${index}`}
              >
                <CardBody>
                  {item.component}
                </CardBody>
              </Collapse>
            </Card>
          ))
        }
      </div>
    );
  }
}

CustomAccordion.propTypes = {
  styleItem: propTypes.object,
  style: propTypes.object,
  items: propTypes.array,
};

CustomAccordion.defaultProps = {
  styleItem: {},
  style: {},
  items: []
};

export default CustomAccordion;



