import React, { Component } from 'react';
import PropTypes from 'prop-types';
import uuid from 'uuid';
import {
  FormGroup,
  Input,
  Label,
  Col
} from 'reactstrap';

import { COLORS } from '../../constants';

class CustomInput extends Component {
  render() {
    const name = uuid.v4();
    const {
      labelPosition,
      placeholder,
      inputStyle,
      label,
      required,
      disabled,
      type,
      onChangeText
    } = this.props;

    if (labelPosition === 'top') {
      return (
        <FormGroup>
          <Label htmlFor={name}>
            {label}
          </Label>
          <Input
            disabled={disabled}
            placeholder={placeholder}
            style={inputStyle}
            name={name}
            type={type}
            onChange={onChangeText}
          />
        </FormGroup>
      );
    }

    return (
      <FormGroup row>
        <Col md="4" style={{ paddingRight: 0 }}>
          <Label htmlFor={name} style={{ paddingTop: 5 }}>
            {required && <span style={{ color: COLORS.RED, marginRight: 2 }}>*</span>}
            {label}
          </Label>
        </Col>
        <Col xs="12" md="8">
          <Input
            disabled={disabled}
            placeholder={placeholder}
            style={[{ borderColor: 'black' }, inputStyle]}
            name={name}
            type={type}
            onChange={onChangeText}
          />
        </Col>
      </FormGroup>
    );
  }
}

CustomInput.propTypes = {
  labelPosition: PropTypes.oneOf(['top', 'left']),
  placeholder: PropTypes.string,
  disabled: PropTypes.bool,
  required: PropTypes.bool,
  inputStyle: PropTypes.object,
  label: PropTypes.string,
  type: PropTypes.string,
  onChangeText: PropTypes.func.isRequired,
};

CustomInput.defaultProps = {
  labelPosition: 'top',
  placeholder: 'Teкстове поле',
  required: false,
  disabled: false,
  inputStyle: {},
  label: '',
  type: 'text',
};

export default CustomInput;



