import React, { Component, Fragment } from 'react';
import { Col, Row } from 'reactstrap';

import { Card } from '../UI';

import { COLORS } from '../../constants';

import UsersItem from './UsersItem';

class UsersList extends Component {

  renderCardHeader = () => (
    <Fragment>
      <strong>
        Результати пошуку користувачів (6)
      </strong>
      <div className="card-header-actions">
        <a className="card-header-action btn btn-minimize" style={{ color: COLORS.BLUE }}>
          <i className="fa fa-refresh" style={{ marginRight: 5 }} />
          <strong>
            Оновити
          </strong>
        </a>
      </div>
    </Fragment>
  );

  render() {
    return (
      <Card collapsable header={this.renderCardHeader()}>
        <Row>
          <Col>
            <UsersItem />
          </Col>
          <Col>
            <UsersItem />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default UsersList;



